.contact {
  display: grid;
  align-content: start;
  row-gap: 16px;
  grid-column: 1 / -1;

  @include on-desktop {
    grid-column: 7 / 12;
  }

  &__name {
    margin-bottom: 8px;

    color: $tertiary-text-page-color;

    font-size: 14px;
    font-weight: 400;
    line-height: 140%;
  }

  &__type {
    display: inline-block;

    @include font-space-mono(14px, $secondary-text-page-color);
    @include hover-underscore;
  }

  &__type--link {
    text-decoration: none;
  }
}
